import React from 'react'
import { useForm } from '@formspree/react';

const ContactForm = () => {
  const [state, handleSubmit] = useForm("mrgyynbw");

  if (state.succeeded) {
    document.getElementById("get-in-touch").reset();
  }

  return (
    <>
      <h2 id="write-me">  </h2>
      <form id="get-in-touch" className="contact-form" onSubmit={handleSubmit}>
        <div className="contact-grid">
          <fieldset>
            <label htmlFor="name">
               : *
            </label>
            <input
              id="name"
              type="text"
              name="name"
              required
            />

            <label htmlFor="email">
               email:
            </label>
            <input
              id="email"
              type="email"
              name="email"
            />

            <label htmlFor="tel">
                  Telegram / Viber: *
            </label>
            <input
              id="tel"
              type="text"
              name="number"
              required
            />

          </fieldset>
          <fieldset>
            <label htmlFor="message">
               : *
            </label>
            <textarea
              id="message"
              name="message"
              required
            />
          </fieldset>

          <fieldset className="form-services">
            <legend> ? *</legend>
            <div className="form-services_radio">
              <input type="radio" id="contactChoice1"
                name="service" value="" required />
              <label htmlFor="contactChoice1"></label>

              <input type="radio" id="contactChoice2"
                name="service" value="" />
              <label htmlFor="contactChoice2"></label>

              <input type="radio" id="contactChoice3"
                name="service" value="" />
              <label htmlFor="contactChoice3"></label>

              <input type="radio" id="contactChoice4"
                name="service" value="" />
              <label htmlFor="contactChoice4"></label>
            </div>

          </fieldset>

        </div>
        <button type="submit" disabled={state.submitting}>
          
        </button>
      </form>
      {state.succeeded && <p>  !</p>}
    </>
  );
}

export default ContactForm